import { RolesEnum, RoutesEnum, Device, Scene, Frame, WifiStatusEnum, DeviceInfo } from '@piloteers/dashboard-model';
import React from 'react';
import * as io from 'socket.io-client';
import './app.styles.scss';
import { Grid } from './components/grid/grid.component';
import { Update } from './components/update/update.component';
import { WifiStatus } from './components/wifi-status/wifi-status.component';

const ENV = require('environment');
declare var window: any;

const deviceId = localStorage.getItem('deviceId') || null;

let socket = io(ENV.SOCKET_URL, { query: `deviceId=${deviceId}&role=${RolesEnum.SCREEN_DEVICE}`, transports: ['websocket'] });

interface IState {
  device: Device;
  scene: Scene;
  frames: Frame[];
  activeFrame: number;
  animation: string;
  isUpdating: boolean;
  wifiStatus: WifiStatusEnum;
  deviceInfo: DeviceInfo;
}

let frameTimeout;
let frameTimeout2;

export class App extends React.Component<any, IState> {
  constructor(props) {
    super(props);
    this.state = {
      device: null,
      scene: null,
      frames: [],
      activeFrame: 0,
      animation: null,
      isUpdating: false,
      wifiStatus: WifiStatusEnum.DISCONNECTED,
      deviceInfo: new DeviceInfo(),
    };

    socket.on('connect', () => {
      console.log('socket: connected ');
    });

    socket.on(RoutesEnum.SD_AUTHENTICATED, (d: Device) => {
      console.log('SD_AUTHENTICATED', d);
      this.setState({ device: d }, () => {
        localStorage.setItem('deviceId', d.resourceId);
        if (d.sceneId) {
          socket.emit(RoutesEnum.DS_SCENE_GET);
          socket.emit(RoutesEnum.DS_FRAME_LIST);
        }
      });
    });

    socket.on(RoutesEnum.SD_DEVICE_GET, (d: Device) => {
      console.log('SD_DEVICE_GET', d);
      if (d.sceneId && d.sceneId != this.state.device.sceneId) {
        socket.emit(RoutesEnum.DS_FRAME_LIST);
        socket.emit(RoutesEnum.DS_SCENE_GET);
      }
      this.setState({ device: d });
    });

    socket.on(RoutesEnum.SD_SCENE_GET, (s: Scene) => {
      console.log('SD_SCENE_GET', s);
      // let sceneHasChanged = this.state.scene == null || s.resourceId != this.state.scene.resourceId;
      // socket.emit(RoutesEnum.DS_FRAME_LIST);
      this.setState({ scene: s, activeFrame: 0 }, () => {
        this.newFrame();
      });
    });

    socket.on(RoutesEnum.SD_FRAME_GET, (f: Frame) => {
      console.log('SD_FRAME_GET', f);
      let frames = this.state.frames;
      let index = frames.findIndex((fr: Frame) => fr.resourceId == f.resourceId);
      if (index > -1) {
        frames[index] = f;
      } else {
        frames.push(f);
      }
      this.setState({ frames }, () => {
        this.newFrame();
      });
    });

    socket.on(RoutesEnum.SD_FRAME_LIST, (f: Frame[]) => {
      console.log('SD_FRAME_LIST', f);
      this.setState({ frames: f }, () => {
        this.newFrame();
      });
    });

    socket.on(RoutesEnum.GD_COMMAND_UPDATE_VERSION, () => {
      console.log('New update comming...');
      this.setState({ isUpdating: true });
    });

    socket.on(RoutesEnum.GD_UPDATE_WIFI_STATUS, (wifiStatus: WifiStatusEnum) => {
      console.log('Update wifi status: ', wifiStatus);
      this.setState({ wifiStatus: wifiStatus });
    });

    socket.on(RoutesEnum.GD_DEVICE_INFO, (info: DeviceInfo) => {
      console.log('Update device info: ', info);
      this.setState({ deviceInfo: info });
    });

    socket.on('disconnect', () => {
      console.log('socket: disconnected');
    });
  }

  newFrame() {
    const { activeFrame, frames, scene } = this.state;
    if (scene && scene.frameIds.length > 1 && frames.length > 1) {
      clearTimeout(frameTimeout);
      clearTimeout(frameTimeout2);
      // console.log('waiting: ', this.getActiveFrame().timeVisible * 1000);
      frameTimeout = setTimeout(() => {
        this.setState({ animation: 'top-slide' }, () => {
          // console.log('waiting slide: ', 1750);
          frameTimeout2 = setTimeout(() => {
            const { activeFrame, frames, scene } = this.state;
            const newActiveFrame = activeFrame + 1 >= scene.frameIds.length ? 0 : activeFrame + 1;
            this.setState({ activeFrame: newActiveFrame, animation: null }, () => {
              this.newFrame();
            });
          }, 1750);
        });
      }, this.getActiveFrame().timeVisible * 1000);
    }
  }

  getActiveFrame() {
    const { frames, scene, activeFrame } = this.state;
    return frames.find((f: Frame) => {
      return f.resourceId == scene.frameIds[activeFrame]
    });
  }

  getActiveNextFrame() {
    const { frames, scene, activeFrame } = this.state;
    const nextActiveFrame = activeFrame + 1 >= scene.frameIds.length ? 0 : activeFrame + 1;
    if (scene.frameIds.length < 2) { return null }
    return frames.find((f: Frame) => f.resourceId == scene.frameIds[nextActiveFrame]);
  }

  render() {
    const { device, frames, scene, animation, isUpdating, wifiStatus, deviceInfo } = this.state;
    let hasFrame: boolean = scene && device && device.sceneId && frames.length > 0;
    let hasNextFrame: boolean = scene && device && device.sceneId && frames.length > 1;
    let frame;
    let nextFrame;
    if (hasFrame) {
      frame = this.getActiveFrame();
    }
    if (hasNextFrame) {
      nextFrame = this.getActiveNextFrame();
    }
    return (
      <div className={`plt-app ${animation ? 'plt-anim__' + animation : ''} `}>
        <React.Fragment>
          {isUpdating ? null : (
            <React.Fragment>
              {hasFrame && frame ? <Grid key={frame.resourceId} frame={frame} /> : null}
              {nextFrame && nextFrame ? <Grid key={nextFrame.resourceId} frame={nextFrame} /> : null}
            </React.Fragment>
          )}
        </React.Fragment>
        <div className='plt-app__device-label'>{device && device.label}{deviceInfo.ip ? `@${deviceInfo.ip}` : null}</div>
        <Update isUpdating={isUpdating} />
        <WifiStatus status={wifiStatus} />
      </div>
    );
  }
}
